import React, { Component } from "react";
import { Container, Form, Button } from "react-bootstrap";
import { Row } from "react-bootstrap";
import { Col } from "react-bootstrap";
import Logo from "../../../Images/logo.png";
import "./Login.css";
import { Link, withRouter } from "react-router-dom";
class Login extends Component {
  login = async () => {
    this.props.history.push("/vendor/home");
  };
  render() {
    return (
      <Container fluid>
        <Row style={{ minHeight: "100vh" }}>
          <Col
            xs="12"
            md="6"
            className="d-none d-md-flex"
            style={{
              background: "white",
              display: "flex",
              alignItems: "center",
              justifyContent: "center"
            }}
          >
            <img style={{ width: "50%" }} src={Logo}></img>
          </Col>
          <Col
            xs="12"
            md="6"
            style={{
              background: "#ba0fb4",
              display: "flex",
              alignItems: "center",
              justifyContent: "center"
            }}
          >
            <Form
              style={{
                background: "#fff",
                padding: 20,
                borderRadius: 8,
                width: "90%"
              }}
            >
              <legend style={{ marginBottom: 40, textAlign: "center" }}>
                Login to your Vendor Account
              </legend>
              <Form.Group controlId="formBasicEmail">
                <Form.Label>Email address</Form.Label>
                <Form.Control type="email" placeholder="Enter email" />
              </Form.Group>

              <Form.Group
                style={{ marginBottom: 20 }}
                controlId="formBasicPassword"
              >
                <Form.Label>Password</Form.Label>
                <Form.Control type="password" placeholder="Password" />
              </Form.Group>
              <Button
                onClick={this.login}
                block
                variant="primary"
                type="button"
              >
                Login
              </Button>
              <Row className="align-middle" style={{ marginTop: 10 }}>
                <Col xs="5">
                  <hr></hr>
                </Col>
                <Col xs="2" className="text-center text-muted">
                  or
                </Col>
                <Col xs="5">
                  <hr></hr>
                </Col>
              </Row>
              <Row style={{ marginTop: 20 }}>
                <Col>
                  <Button block className="facebook bg-primary " type="button">
                    Facebook Login
                  </Button>
                </Col>
                <Col>
                  <Button block className="google bg-success" type="button">
                    Google Login
                  </Button>
                </Col>
              </Row>
              <hr style={{ marginTop: 30, marginBottom: 20 }}></hr>
              <p className="text-center">
                Don't have an account? <Link>Click here to Register</Link>
              </p>
            </Form>
          </Col>
        </Row>
      </Container>
    );
  }
}
export default withRouter(Login);
