import React, { Component } from "react";
import "./VendorSidebar.css";
import Logo from "../../Images/logo.png";
import { Link } from "react-router-dom";
import { Badge, Dropdown } from "react-bootstrap";
export default class VendorSidebar extends Component {
  render() {
    return (
      <div>
        <nav className="navbar navbar-expand-lg navbar-light fixed-top bg-white">
          <Link className="navbar-brand" to="/vendor/">
            <img style={{ width: "80px" }} src={Logo}></img>
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarCollapse"
            aria-controls="navbarCollapse"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon "></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarCollapse">
            <ul
              className="navbar-nav mr-auto vendor-sidebar h-100 bg-white"
              id="navAccordion"
            >
              <li className="nav-item active">
                <div className="d-none d-md-flex usaneTitle">
                  <Link
                    className="d-flex justify-content-center align-items-center"
                    to="/vendor/"
                  >
                    <img style={{ width: "80%" }} src={Logo}></img>
                  </Link>
                </div>
                <div className="mx-3">
                  <li className="nav-item">
                    <Link to="/vendor/" className="nav-link nav-link-custom ">
                      <span className="ml-2">Upcoming Reservations</span>
                      <div className="hr"></div>
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link to="/vendor/" className="nav-link nav-link-custom ">
                      <span className="ml-2">Past Reservations</span>
                      <div className="hr"></div>
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link to="/vendor/" className="nav-link nav-link-custom ">
                      <span className="ml-2">Edit Profile</span>
                      <div className="hr"></div>
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link to="/vendor/" className="nav-link nav-link-custom ">
                      <span className="ml-2">Complaint</span>
                      <div className="hr"></div>
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link to="/vendor/" className="nav-link nav-link-custom ">
                      <span className="ml-2">Reviews</span>
                      <div className="hr"></div>
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link to="/vendor/" className="nav-link nav-link-custom ">
                      <span className="ml-2">Logout</span>
                      <div className="hr"></div>
                    </Link>
                  </li>
                </div>
              </li>
            </ul>
            <form className="form-inline ml-auto mt-2 mt-md-0"></form>
            <div className="d-flex align-items-center p-2">
              <Dropdown>
                <Dropdown.Toggle
                  style={{ width: 35 }}
                  variant="link"
                  id="dropdown-basic"
                >
                  <img
                    style={{
                      width: "35px",
                      objectFit: "cover",
                      height: "35px",
                      borderRadius: "35px",
                      border: "solid 1px #ba0fb4"
                    }}
                    src={
                      "https://www.rd.com/wp-content/uploads/2017/09/01-shutterstock_476340928-Irina-Bg-1024x683.jpg"
                    }
                  ></img>
                  <Badge
                    style={{
                      position: "absolute",
                      top: 2,
                      left: 28,
                      height: 14,
                      width: 14,
                      borderRadius: 14,
                      background: "#ba0fb4",
                      color: "#fff",
                      fontWeight: "bold"
                    }}
                    pill
                  >
                    3
                  </Badge>
                </Dropdown.Toggle>

                <Dropdown.Menu>
                  <Dropdown.Item>No notifications</Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>
            </div>
          </div>
        </nav>
        <main className="content-wrapper">
          <div className="container">{this.props.children}</div>
        </main>
      </div>
    );
  }
}
