import React, { Component } from "react";
import Footer from "../Footer/Footer";

export default class FooterAndButtons extends Component {
  render() {
    return (
      <>
        <div className="container">
          <div className="flex social-btns">
            <a className="app-btn blu flex vert" href="https://www.apple.com">
              <i className="fab fa-apple"></i>
              <p className="text">
                Available on the <br />{" "}
                <span className="big-txt">App Store</span>
              </p>
            </a>

            <a className="app-btn blu flex vert" href="https://www.google.com">
              <i className="fab fa-google-play"></i>
              <p className="text">
                Get it on <br /> <span className="big-txt">Google Play</span>
              </p>
            </a>
          </div>
        </div>
        <div className="container-fluid">
          <Footer></Footer>
        </div>
      </>
    );
  }
}
