import React, { Component } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  Table,
  Media,
  Badge
} from "react-bootstrap";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import Footer from "../../../Components/FooterAndButtons/FooterAndButtons";
import Header from "../../../Components/Sidebar/Header";
class Customer extends Component {
  clickHandler = async uid => {
    this.props.history.push("/admin/vendor/" + uid);
  };
  render() {
    const customer = this.props.customers.find(
      e => e.uid === this.props.match.params.uid
    );
    const reservations = this.props.reservations.filter(
      r => r.customer_uid == this.props.match.params.uid
    );
    const complaints = this.props.complaints.filter(
      r => r.customer_uid == this.props.match.params.uid
    );
    const reviews = this.props.reviews.filter(
      r => r.customer_uid == this.props.match.params.uid
    );
    const reservationsToRender = reservations.map(reservation => {
      const vendor = this.props.vendors.find(
        e => e.uid === reservation.restaurant_uid
      );
      const badgeVariant =
        reservation.status == "Reserved"
          ? "warning"
          : reservation.status == "Completed"
          ? "success"
          : "danger";
      return (
        <tr onClick={() => this.clickHandler(vendor.uid)}>
          <td>
            <img
              src={vendor ? vendor.photo_url : ""}
              style={{
                width: 40,
                height: 40,
                objectFit: "cover",
                borderRadius: 40
              }}
            ></img>
          </td>
          <td>{vendor ? vendor.name : "Vendor Deleted"}</td>
          <td>{reservation.time}</td>
          <td>{reservation.date}</td>
          <td>{reservation.attandee}</td>
          <td>
            {Math.floor(Number(reservation.attandee / 3)) == 0
              ? 1
              : Math.floor(Number(reservation.attandee / 3))}
          </td>
          <td>
            <Badge variant={badgeVariant}>{reservation.status}</Badge>
          </td>
        </tr>
      );
    });
    const reviewsToRender = reviews.map(review => {
      const vendor = this.props.vendors.find(
        e => e.uid === review.restaurant_uid
      );

      const array = [
        "star-outline",
        "star-outline",
        "star-outline",
        "star-outline",
        "star-outline"
      ];
      array.fill("star", 0, review.rating);
      return (
        <Media as="li">
          <img
            width={64}
            height={64}
            className="mr-3"
            style={{ objectFit: "cover" }}
            src={
              vendor.photo_url
                ? vendor.photo_url
                : "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRwYsBVC-oKRvO87aXVfE63jYe52NcDYM5ArsCRKAGZaImUHAql"
            }
            alt="Generic placeholder"
          />
          <Media.Body>
            <h5>{vendor.name}</h5>
            <Row className="media-stars ">
              {array.map(e => (
                <ion-icon name={e}></ion-icon>
              ))}
            </Row>
            <p>{review.comment}</p>
          </Media.Body>
        </Media>
      );
    });
    const complaintsToRender = complaints.map(complaint => {
      const vendor = this.props.vendors.find(
        e => e.uid === complaint.restaurant_uid
      );

      return (
        <Media as="li">
          <img
            width={64}
            height={64}
            className="mr-3"
            style={{ objectFit: "cover" }}
            src={
              vendor.photo_url
                ? vendor.photo_url
                : "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRwYsBVC-oKRvO87aXVfE63jYe52NcDYM5ArsCRKAGZaImUHAql"
            }
            alt="Generic placeholder"
          />
          <Media.Body>
            <h5>{vendor.name}</h5>
            <Row className="ml-1">
              <h6 className="text-muted">{vendor.title}</h6>
            </Row>
            <p>{complaint.description}</p>
            <p>
              Date of Complaint:{" "}
              <small className="text-muted">{complaint.date}</small>
            </p>
          </Media.Body>
        </Media>
      );
    });
    return (
      <div>
        <Header>
          <Container>
            <Row>
              <Col xs="12" md="3">
                <Card>
                  <Card.Body>
                    <img
                      style={{ width: "100%", height: 200, objectFit: "cover" }}
                      src={customer.photo}
                    ></img>
                    <hr></hr>
                    <p className="h5">{customer.name}</p>
                    <hr></hr>
                    <p className="h6">
                      Phone:{" "}
                      <span className="text-muted">{customer.number}</span>
                    </p>
                    <p className="h6">
                      Email:{" "}
                      <span className="text-muted">{customer.email}</span>
                    </p>
                  </Card.Body>
                </Card>
              </Col>
              <Col className="mt-3 mt-md-0 mb-5" xs="12" md="9">
                <Card>
                  <Card.Header>
                    <h1>Customer History</h1>
                  </Card.Header>
                  <Card.Body>
                    <h3>Reservations</h3>
                    <Table responsive striped hover>
                      <thead>
                        <tr>
                          <th>Image</th>
                          <th>Vendor Name</th>
                          <th>Reservation Time</th>
                          <th>Date of Registration</th>
                          <th>Number of Attendees</th>
                          <th>Number of Tables</th>
                          <th>Status</th>
                        </tr>
                      </thead>
                      <tbody>{reservationsToRender}</tbody>
                    </Table>

                    <hr></hr>
                    {reviewsToRender.length > 0 ? (
                      <>
                        <h3>Reviews</h3>
                        <ul className="list-unstyled mt-5">
                          {reviewsToRender}
                        </ul>
                      </>
                    ) : (
                      <p>No Reviews by this customer</p>
                    )}
                    <hr></hr>

                    {complaintsToRender.length > 0 ? (
                      <>
                        <h3>Complaints</h3>
                        <ul className="list-unstyled mt-5">
                          {complaintsToRender}
                        </ul>
                      </>
                    ) : (
                      <p>No Complaints by this customer</p>
                    )}
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
          <Footer></Footer>
        </Header>
      </div>
    );
  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Customer));
