import React, { Component } from "react";
import AdminNavbar from "../../../Components/AdminNavbar/AdminNavbar";
import {
  Container,
  Col,
  Card,
  Button,
  Pagination,
  Table,
  Badge,
  Media,
  Form,
  Modal,
  InputGroup,
  FormControl
} from "react-bootstrap";
import { Row } from "react-bootstrap";
import "./Vendor.css";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import Footer from "../../../Components/FooterAndButtons/FooterAndButtons";
import Firebase from "../../../Firebase/Firebase";
import Header from "../../../Components/Sidebar/Header";
class Vendor extends Component {
  state = {
    address: "",
    branch: "",
    category: "",
    close: false,
    email: "",
    instant_data: {
      available: 0
    },
    modalShow: false,
    is_email_verfied: false,
    is_number_verified: false,
    is_restrict_order: false,
    is_verified: false,
    llocation: {
      lat: 0,
      llong: 0
    },
    name: "",
    number: "",
    photo_url: "",
    reservation_fee: "0",
    processing_fee: "0",
    uid: "LEa50vSb5KTbwvz3FgToiDwvzKm1",
    bank_details: {
      accountNumber: "",
      account_name: "",
      bank_name: ""
    },
    reviews: [],
    newProFee: 0
  };
  restrictOrder = async uid => {
    await Firebase.restrictOrder(!this.state.is_restrict_order, uid);
    this.setState({
      is_restrict_order: !this.state.is_restrict_order
    });
  };
  checkDate = date => {
    var weekStart = new Date();
    weekStart.setDate(weekStart.getDate() - weekStart.getDay());
    weekStart.setDate(weekStart.getDate() - 30);
    var weekEnd = new Date(+weekStart);
    weekEnd.setDate(weekEnd.getDate() + 30);
    return date >= weekStart && date <= weekEnd;
  };
  componentDidMount = async () => {
    const vendor = this.props.vendors.find(
      element => element.uid === this.props.match.params.uid
    );
    console.log(vendor);
    this.setState({
      ...vendor
    });
    const reviews = this.props.reviews.filter(
      element => element.restaurant_uid === this.props.match.params.uid
    );
    let reviewsCustomerAndVendorJoin = reviews.map(element => {
      const customer = this.props.customers.find(
        c => c.uid === element.customer_uid
      );
      const restaurant = this.props.vendors.find(
        c => c.uid === element.restaurant_uid
      );
      const array = [
        "star-outline",
        "star-outline",
        "star-outline",
        "star-outline",
        "star-outline"
      ];
      array.fill("star", 0, element.rating);
      return {
        ...element,
        customer,
        restaurant,
        array
      };
    });
    this.setState({
      reviews: reviewsCustomerAndVendorJoin
    });
  };
  clickHandler = async uid => {
    this.props.history.push("/admin/customer/" + uid);
  };
  deleteHandler = async () => {
    await Firebase.deleteVendor(this.state.uid);
    this.props.history.push("/admin/");
  };
  render() {
    const reservations = this.props.reservations.filter(
      element => element.restaurant_uid === this.props.match.params.uid
    );
    console.log(this.props.reservations);
    const reservationsToRender = reservations.map(element => {
      const customer = this.props.customers.find(
        c => c.uid === element.customer_uid
      );
      const vendor = this.props.vendors.find(
        c => c.uid === element.restaurant_uid
      );
      const badgeVariant =
        element.status == "Reserved"
          ? "warning"
          : element.status == "Completed"
          ? "success"
          : "danger";
      return (
        <tr onClick={() => this.clickHandler(customer.uid)}>
          <td>
            <img
              style={{
                width: 40,
                height: 40,
                borderRadius: 50,
                objectFit: "cover"
              }}
              src={customer.photo}
            ></img>
          </td>
          <td>{customer.name}</td>
          <td>{element.time}</td>
          <td>{element.date}</td>
          <td>{element.attandee}</td>
          <td>
            {Math.floor(Number(element.attandee / 3)) == 0
              ? 1
              : Math.floor(Number(element.attandee / 3))}
          </td>
          <td>
            <Badge variant={badgeVariant}>{element.status}</Badge>
          </td>
        </tr>
      );
    });
    console.log(reservationsToRender);
    const complaints = this.props.complaints.filter(
      element => element.restaurant_uid === this.props.match.params.uid
    );
    const complaintsToRender = complaints.map(element => {
      const customer = this.props.customers.find(
        c => c.uid === element.customer_uid
      );
      const vendor = this.props.vendors.find(
        c => c.uid === element.restaurant_uid
      );
      return customer ? (
        <Media as="li">
          <img
            width={64}
            height={64}
            className="mr-3"
            style={{ objectFit: "cover" }}
            src={
              customer.photo
                ? customer.photo
                : "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRwYsBVC-oKRvO87aXVfE63jYe52NcDYM5ArsCRKAGZaImUHAql"
            }
            alt="Generic placeholder"
          />
          <Media.Body>
            <h5>{customer.name}</h5>
            <Row className="ml-1">
              <h6 onClick={this.redirectToVendor}>{vendor.name}</h6>
            </Row>
            <Row className="ml-1">
              <h6 className="text-muted">{element.title}</h6>
            </Row>
            <p>{element.description}</p>
            <p>
              Date of Complaint:{" "}
              <small className="text-muted">{element.date}</small>
            </p>
          </Media.Body>
        </Media>
      ) : null;
    });
    return (
      <div>
        <Header>
          <Container>
            <Row>
              <Col xs="12" md="3">
                <Card>
                  <Card.Body>
                    <img
                      style={{
                        width: "100%",
                        height: 200,
                        objectFit: "cover"
                      }}
                      src={this.state.photo_url}
                    ></img>
                    <hr></hr>
                    <p className="h5">
                      {this.state.name}
                      {this.state.is_verified ? null : (
                        <small className="text-muted text-danger">
                          {" "}
                          (Verification Pending)
                        </small>
                      )}
                    </p>
                    <p className="text-muted">{this.state.category}</p>
                    <hr></hr>
                    <p className="h6">
                      Branch:{" "}
                      <span className="text-muted">{this.state.branch}</span>
                    </p>
                    <p className="h6">
                      Phone:{" "}
                      <span className="text-muted">{this.state.number}</span>
                    </p>
                    <p className="h6">
                      Email:{" "}
                      <span className="text-muted">{this.state.email}</span>
                    </p>
                    <hr></hr>
                    <p className="h6">
                      Bank Name:{" "}
                      <span className="text-muted">
                        {this.state.bank_details.bank_name}
                      </span>
                    </p>
                    <p className="h6">
                      Account Name:{" "}
                      <span className="text-muted">
                        {this.state.bank_details.account_name}
                      </span>
                    </p>
                    <p className="h6">
                      Account Number:{" "}
                      <span className="text-muted">
                        {this.state.bank_details.accountNumber}
                      </span>
                    </p>
                    <hr></hr>
                    <p className="h6">
                      Reservation Fee:{" "}
                      <span className="text-muted">
                        EGP {this.state.reservation_fee}
                      </span>
                    </p>
                    <p className="h6">
                      Processing Fee:{" "}
                      <span className="text-muted">
                        EGP {this.state.processing_fee}
                      </span>
                    </p>
                    <hr></hr>
                    <p className="h6">
                      Last Months Reservation Revenue:{" "}
                      <span className="text-muted">
                        EGP{" "}
                        {this.state.reservation_fee *
                          reservations.filter(e => {
                            const date = new Date(e.date);
                            return this.checkDate(date);
                          }).length}
                      </span>
                    </p>
                    <p className="h6">
                      Last Months Processing Revenue:{" "}
                      <span className="text-muted">
                        EGP{" "}
                        {this.state.processing_fee *
                          reservations.filter(e => {
                            const date = new Date(e.date);
                            return this.checkDate(date);
                          }).length}
                      </span>
                    </p>
                    <hr></hr>
                    <p className="h6">
                      Available Reservations:{" "}
                      <span className="text-muted">
                        {this.state.instant_data.available}
                      </span>
                    </p>
                    <hr></hr>
                    <Form.Check
                      checked={this.state.is_restrict_order}
                      type="switch"
                      id="custom-switch"
                      label="Stop Reservations"
                      onChange={() => this.restrictOrder(this.state.uid)}
                    />
                  </Card.Body>
                </Card>
              </Col>
              <Col className="mt-3 mt-md-0 mb-5" xs="12" md="9">
                <Card>
                  <Card.Header>
                    <h1>Vendor Details</h1>
                  </Card.Header>
                  <Card.Body>
                    <Row className=" p-4">
                      <Button
                        onClick={() => {
                          this.setState({
                            modalShow: true
                          });
                        }}
                        variant="primary"
                        className="mx-2"
                      >
                        {this.state.is_verified
                          ? "Edit Processing Fee"
                          : "Approve Verification"}
                      </Button>
                      <Button
                        onClick={this.deleteHandler}
                        variant="danger"
                        className="mx-2"
                      >
                        {this.state.is_verified
                          ? "Remove Vendor"
                          : "Reject Verification"}
                      </Button>
                    </Row>
                    <hr></hr>
                    <h3>Location</h3>
                    <div className="map">
                      <h1>Map will be displayed here</h1>
                    </div>
                    <hr></hr>
                    {reservationsToRender.length > 0 ? (
                      <>
                        <h3>Reservations</h3>
                        <Table responsive striped hover>
                          <thead>
                            <tr>
                              <th>Image</th>
                              <th>Customer Name</th>
                              <th>Reservation Time</th>
                              <th>Date of Reservation</th>
                              <th>Number of Attendees</th>
                              <th>Number of Tables</th>
                              <th>Status</th>
                            </tr>
                          </thead>
                          <tbody>{reservationsToRender}</tbody>
                        </Table>
                      </>
                    ) : (
                      <p>No Reservations Found</p>
                    )}
                    <hr></hr>
                    {this.state.reviews.length > 0 ? (
                      <>
                        <h3>Reviews</h3>
                        <ul className="list-unstyled mt-5">
                          {this.state.reviews.map(element => {
                            return (
                              <Media as="li">
                                <img
                                  width={64}
                                  height={64}
                                  className="mr-3"
                                  style={{ objectFit: "cover" }}
                                  src={
                                    element.customer.photo
                                      ? element.customer.photo
                                      : "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRwYsBVC-oKRvO87aXVfE63jYe52NcDYM5ArsCRKAGZaImUHAql"
                                  }
                                  alt="Generic placeholder"
                                />
                                <Media.Body>
                                  <h5>{element.customer.name}</h5>
                                  <Row className="media-stars ">
                                    {element.array.map(star => {
                                      return <ion-icon name={star}></ion-icon>;
                                    })}
                                  </Row>
                                  <p>{element.comment}</p>
                                </Media.Body>
                              </Media>
                            );
                          })}
                        </ul>
                      </>
                    ) : (
                      <p>No Reviews for this Vendor</p>
                    )}
                    <hr></hr>
                    {complaintsToRender.length > 0 ? (
                      <>
                        <h3>Complaints</h3>
                        <ul className="list-unstyled mt-5">
                          {complaintsToRender}
                        </ul>
                      </>
                    ) : (
                      <p>No Complaints for this Vendor</p>
                    )}
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
          <Footer></Footer>
        </Header>
        <Modal
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
          show={this.state.modalShow}
          onHide={() => this.setState({ modalShow: false })}
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Edit Processing Fee
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <InputGroup className="mb-3">
              <InputGroup.Prepend>
                <InputGroup.Text id="inputGroup-sizing-default">
                  Processing Fee
                </InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                aria-label="Default"
                aria-describedby="inputGroup-sizing-default"
                type="number"
                onChange={e => {
                  this.setState({
                    newProFee: e.target.value
                  });
                }}
              />
            </InputGroup>
          </Modal.Body>
          <Modal.Footer>
            <Button
              onClick={async () => {
                if (this.state.is_verified) {
                  await Firebase.updateProcessingFee(
                    this.state.newProFee,
                    this.state.uid
                  );
                } else {
                  await Firebase.updateProcessingFeeAndVerify(
                    this.state.newProFee,
                    this.state.uid
                  );
                }
                this.setState({ modalShow: false });
                window.location.reload();
              }}
            >
              {this.state.is_verified ? "Update" : "Verify"}
            </Button>
            <Button
              variant="danger"
              onClick={() => this.setState({ modalShow: false })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Vendor));
