import React, { Component } from "react";
import * as emailjs from "emailjs-com";
import HomeNavbar from "../../Components/HomeNavbar/HomeNavbar";
import "./Contact.css";
import { withRouter } from "react-router-dom";
import FooterAndButtons from "../../Components/FooterAndButtons/FooterAndButtons";
class Contact extends Component {
  state = {
    email: "",
    subject: "Complaint",
    message: ""
  };
  changeState = (name, value) => {
    this.setState({
      [name]: value
    });
  };
  sendMail = async () => {
    if (
      this.state.email.trim() !== "" &&
      this.state.subject.trim() !== "" &&
      this.state.message.trim() !== ""
    ) {
      let templateParams = {
        from_name: this.state.email.trim(),
        to_name: "ibrahimnabi80@gmail.com",
        reply_to: this.state.email.trim(),
        subject: this.state.subject,
        message_html: this.state.message
      };
      emailjs.send(
        "reserve_contact",
        "template_KiH1F4zS",
        templateParams,
        "user_aVbbjsfKbS0Qub1NMA29M"
      );
      let templateParams2 = {
        from_name: "ibrahimnabi80@gmail.com",
        to_name: this.state.email.trim(),
        subject: "Response",
        message_html:
          "We received your message and we will reply soon. Your inquiry id is 001"
      };
      emailjs.send(
        "reserve_contact",
        "template_KiH1F4zS",
        templateParams2,
        "user_aVbbjsfKbS0Qub1NMA29M"
      );
      alert("We received your message and we will reply soon");
      this.props.history.push("/");
    } else {
      alert("Please fill all field before submitting.");
    }
  };
  render() {
    return (
      <div>
        <HomeNavbar></HomeNavbar>
        <div className="container">
          <form className=" contact-form">
            <legend>Contact Form</legend>
            <div className="form-group">
              <label for="exampleFormControlSelect1">Select Subject</label>
              <select
                onChange={e => this.changeState("subject", e.target.value)}
                className="form-control"
                id="exampleFormControlSelect1"
              >
                <option value="Complaint">Complaint</option>
                <option value="Inquiry">Inquiry</option>
                <option value="Feedback">Feedback</option>
              </select>
            </div>
            <div className="form-group">
              <label for="exampleInputEmail1">Enter Your Email Address</label>
              <input
                onChange={e => this.changeState("email", e.target.value)}
                type="email"
                className="form-control"
                id="exampleInputEmail1"
                placeholder="Email Address"
                aria-describedby="emailHelp"
              />
            </div>
            <div className="form-group">
              <label for="exampleFormControlTextarea1">
                Enter Your Message
              </label>
              <textarea
                onChange={e => this.changeState("message", e.target.value)}
                className="form-control"
                id="exampleFormControlTextarea1"
                rows="3"
                placeholder="Message"
              ></textarea>
            </div>
            <button
              onClick={this.sendMail}
              type="button"
              className="btn btn-primary btn-block"
            >
              Submit
            </button>
          </form>
        </div>
        <FooterAndButtons></FooterAndButtons>
      </div>
    );
  }
}
export default withRouter(Contact);
