import { AdminActionTypes } from "../Actions/ActionTypes";

const mapStateToProps = state => {
  return state.AdminReducer;
};
const mapDispatchToProps = dispatch => {
  return {
    updateLogin: user =>
      dispatch({
        type: AdminActionTypes.ADMIN_UPDATE_LOGIN_STATUS,
        payload: user
      }),
    updateComplaints: complaints =>
      dispatch({
        type: AdminActionTypes.UPDATE_COMPLAINTS,
        payload: complaints
      }),
    updateDiscountVouchers: vouchers =>
      dispatch({
        type: AdminActionTypes.UPDATE_DISCOUNT_VOUCHERS,
        payload: vouchers
      }),
    updateNotifications: notifications =>
      dispatch({
        type: AdminActionTypes.UPDATE_NOTIFICATIONS,
        payload: notifications
      }),
    updateReservations: reservations =>
      dispatch({
        type: AdminActionTypes.UPDATE_RESERVATIONS,
        payload: reservations
      }),
    updateReviews: reviews =>
      dispatch({ type: AdminActionTypes.UPDATE_REVIEWS, payload: reviews }),
    updateCustomers: customers =>
      dispatch({ type: AdminActionTypes.UPDATE_CUSTOMERS, payload: customers }),
    updateVendors: vendors =>
      dispatch({ type: AdminActionTypes.UPDATE_VENDORS, payload: vendors }),
    updateSlideshows: slideshows =>
      dispatch({ type: AdminActionTypes.UPDATE_SLIDESHOW, payload: slideshows })
  };
};
export { mapDispatchToProps, mapStateToProps };
