import React, { Component } from "react";
import "../App.css";
import { Route, Switch, HashRouter } from "react-router-dom";
import Home from "../Pages/Home/Home";
import TermsAndConditions from "../Pages/TermsAndConditions/TermsAndConditions";
import PrivacyPolicy from "../Pages/PrivacyPolicy/PrivacyPolicy";
import Contact from "../Pages/Contact/Contact";
import AdminLogin from "../Pages/Admin/Login/Login";
import Dashboard from "../Pages/Admin/Dashboard/Dashboard";
import Vendor from "../Pages/Admin/Vendor/Vendor";
import Complaints from "../Pages/Admin/Complaints/Complaints";
import Reservations from "../Pages/Admin/Reservations/Reservations";
import Customer from "../Pages/Admin/Customer/Customer";
import DiscountVouchers from "../Pages/Admin/DiscountVouchers/DiscountVouchers";
import Slideshow from "../Pages/Admin/Slideshow/Slideshow";
import { mapDispatchToProps, mapStateToProps } from "../Config/AdminMapping";
import { connect } from "react-redux";
import { getAdmin } from "../Config/localStorage";
import Firebase from "../Firebase/Firebase";
import { Spinner } from "react-bootstrap";

import VendorLogin from "../Pages/Vendor/Login/Login";
import VendorHome from "../Pages/Vendor/Home/Home";
import VendorVerifications from "../Pages/Admin/VendorVerifications/VendorVerifications";
class Router extends Component {
  state = {
    loading: true
  };
  componentDidMount = async () => {
    const admin = getAdmin();
    if (admin) {
      const vendors = await Firebase.getRestaurants();
      this.props.updateVendors(vendors);
      const reviews = await Firebase.getAllReviews();
      this.props.updateReviews(reviews);
      const complaints = await Firebase.getComplaints();
      this.props.updateComplaints(complaints);
      const customers = await Firebase.getCustomers();
      this.props.updateCustomers(customers);
      const vouchers = await Firebase.getDiscountVouchers();
      this.props.updateDiscountVouchers(vouchers);
      const notifications = await Firebase.getNotifications(admin.uid);
      this.props.updateNotifications(notifications);
      const reservations = await Firebase.getReservations();
      this.props.updateReservations(reservations);
      const slideshows = await Firebase.getSlideShows();
      this.props.updateSlideshows(slideshows);
      Firebase.listenToVendors(snapshot => {
        const updatedValue = snapshot.val();
        let newVendors = [...this.props.vendors];
        const foundIndex = newVendors.findIndex(e => e.uid == updatedValue.uid);
        if (foundIndex >= 0) {
          newVendors[foundIndex] = updatedValue;
        } else {
          newVendors.push(updatedValue);
        }
        this.props.updateVendors(newVendors);
      });
      Firebase.listenToComplaints(snapshot => {
        const updatedValue = snapshot.val();
        let newComplaints = [...this.props.complaints];
        const foundIndex = newComplaints.findIndex(
          e => e.uid == updatedValue.uid
        );
        if (foundIndex >= 0) {
          newComplaints[foundIndex] = updatedValue;
        } else {
          newComplaints.push(updatedValue);
        }
        this.props.updateComplaints(newComplaints);
      });
      Firebase.listenToCustomers(snapshot => {
        const updatedValue = snapshot.val();
        let newCustomers = [...this.props.customers];
        const foundIndex = newCustomers.findIndex(
          e => e.uid == updatedValue.uid
        );
        if (foundIndex >= 0) {
          newCustomers[foundIndex] = updatedValue;
        } else {
          newCustomers.push(updatedValue);
        }
        this.props.updateCustomers(newCustomers);
      });
      Firebase.listenToDiscountVouchers(snapshot => {
        const updatedValue = snapshot.val();
        let newVouchers = [...this.props.vouchers];
        const foundIndex = newVouchers.findIndex(
          e => e.uid == updatedValue.uid
        );
        if (foundIndex >= 0) {
          newVouchers[foundIndex] = updatedValue;
        } else {
          newVouchers.push(updatedValue);
        }
        this.props.updateDiscountVouchers(newVouchers);
      });
      Firebase.listenToNotifications(snapshot => {
        const updatedValue = snapshot.val();
        let newNotifications = [...this.props.notifications];
        const foundIndex = newNotifications.findIndex(
          e => e.uid == updatedValue.uid
        );
        if (foundIndex >= 0) {
          newNotifications[foundIndex] = updatedValue;
        } else {
          newNotifications.push(updatedValue);
        }
        this.props.updateNotifications(newNotifications);
      });
      Firebase.listenToReservations(snapshot => {
        const updatedValue = snapshot.val();
        let newReservations = [...this.props.reservations];
        const foundIndex = newReservations.findIndex(
          e => e.uid == updatedValue.uid
        );
        if (foundIndex >= 0) {
          newReservations[foundIndex] = updatedValue;
        } else {
          newReservations.push(updatedValue);
        }
        this.props.updateReservations(newReservations);
      });
      Firebase.listenToReviews(snapshot => {
        const updatedValue = snapshot.val();
        let newReviews = [...this.props.reviews];
        const foundIndex = newReviews.findIndex(e => e.uid == updatedValue.uid);
        if (foundIndex >= 0) {
          newReviews[foundIndex] = updatedValue;
        } else {
          newReviews.push(updatedValue);
        }
        this.props.updateReviews(newReviews);
      });

      Firebase.listenToSlideShows(snapshot => {
        const updatedValue = snapshot.val();
        let newSlideshows = [...this.props.slideshows];
        const foundIndex = newSlideshows.findIndex(
          e => e.uid == updatedValue.uid
        );
        if (foundIndex >= 0) {
          newSlideshows[foundIndex] = updatedValue;
        } else {
          newSlideshows.push(updatedValue);
        }
        this.props.updateSlideshows(newSlideshows);
      });
      this.props.updateLogin(admin);
    }
    this.setState({ loading: false });
  };
  render() {
    return this.state.loading ? (
      <div
        style={{
          height: "100vh",
          width: "100%",
          display: "flex",
          alignItems: "center",
          justifyContent: "center"
        }}
      >
        <Spinner
          style={{ width: 65, height: 65 }}
          animation="border"
          variant="primary"
        />
      </div>
    ) : (
      <HashRouter basename="/">
        <Switch>
          <Route exact path="/">
            <Home></Home>
          </Route>
          <Route exact path="/contact">
            <Contact></Contact>
          </Route>
          <Route exact path="/terms">
            <TermsAndConditions></TermsAndConditions>
          </Route>
          <Route exact path="/privacy">
            <PrivacyPolicy></PrivacyPolicy>
          </Route>
          {this.props.loggedInAdmin !== null ? (
            <>
              <Route exact path="/admin/">
                <Dashboard></Dashboard>
              </Route>
              <Route exact path="/admin/vendor/:uid">
                <Vendor></Vendor>
              </Route>
              <Route exact path="/admin/complaints">
                <Complaints></Complaints>
              </Route>
              <Route exact path="/admin/customer/:uid">
                <Customer></Customer>
              </Route>
              <Route exact path="/admin/discount">
                <DiscountVouchers></DiscountVouchers>
              </Route>
              <Route exact path="/admin/reservations">
                <Reservations></Reservations>
              </Route>
              <Route exact path="/admin/slideshow">
                <Slideshow></Slideshow>
              </Route>
              <Route exact path="/admin/verification">
                <VendorVerifications></VendorVerifications>
              </Route>
            </>
          ) : (
            <Route exact path="/admin/">
              <AdminLogin></AdminLogin>
            </Route>
          )}
          <Route exact path="/vendor/">
            <VendorLogin></VendorLogin>
          </Route>
          <Route exact path="/vendor/home">
            <VendorHome></VendorHome>
          </Route>
        </Switch>
      </HashRouter>
    );
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Router);
