import React, { Component } from "react";
import AdminNavbar from "../../../Components/AdminNavbar/AdminNavbar";
import { Card, Pagination, Row, Media, Container } from "react-bootstrap";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import Footer from "../../../Components/FooterAndButtons/FooterAndButtons";
import Header from "../../../Components/Sidebar/Header";

class Complaints extends Component {
  redirectToVendor = async () => {
    this.props.history.push("/admin/vendor");
  };
  render() {
    const complaintsToRender = this.props.complaints.map(element => {
      const customer = this.props.customers.find(
        c => c.uid === element.customer_uid
      );
      const vendor = this.props.vendors.find(
        c => c.uid === element.restaurant_uid
      );
      const condition = customer && vendor;
      return condition ? (
        <>
          <Media as="li">
            <img
              width={64}
              height={64}
              className="mr-3"
              style={{ objectFit: "cover" }}
              src={
                customer.photo
                  ? customer.photo
                  : "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRwYsBVC-oKRvO87aXVfE63jYe52NcDYM5ArsCRKAGZaImUHAql"
              }
              alt="Generic placeholder"
            />
            <Media.Body>
              <h5>{customer.name}</h5>
              <Row className="ml-1">
                <h6 onClick={this.redirectToVendor}>{vendor.name}</h6>
              </Row>
              <Row className="ml-1">
                <h6 className="text-muted">{element.title}</h6>
              </Row>
              <p>{element.description}</p>
              <p>
                Date of Complaint:{" "}
                <small className="text-muted">{element.date}</small>
              </p>
            </Media.Body>
          </Media>
          <hr></hr>
        </>
      ) : null;
    });
    return (
      <div>
        <Header>
          <Container>
            <Card className="my-3">
              <Card.Header>
                <h1>Complaints</h1>
              </Card.Header>
              <Card.Body>
                <ul className="list-unstyled mt-5">{complaintsToRender}</ul>
              </Card.Body>
            </Card>
          </Container>

          <Footer></Footer>
        </Header>
      </div>
    );
  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Complaints));
