import React, { Component } from "react";
import Logo from "../../Images/logo.png";
import "./AdminNavbar.css";
import { Link, withRouter } from "react-router-dom";
import { Navbar } from "react-bootstrap";
import { Nav } from "react-bootstrap";
import { Button } from "react-bootstrap";
import { Dropdown } from "react-bootstrap";
import { Badge } from "react-bootstrap";
import { connect } from "react-redux";
import { removeAdmin } from "../../Config/localStorage";
import { mapStateToProps, mapDispatchToProps } from "../../Config/AdminMapping";
class AdminNavbar extends Component {
  logout = async () => {
    removeAdmin();
    this.props.updateLogin(null);
    this.props.history.push("/admin/");
  };
  render() {
    const vendors = this.props.vendors.filter(e => !e.is_verified);
    const notifications = vendors.map(e => {
      return (
        <Dropdown.Item>
          <Link to={"/admin/vendor/" + e.uid}>Verify New Vendor {e.name}</Link>
        </Dropdown.Item>
      );
    });
    return (
      <Navbar className="admin-nav" bg="light" expand="lg">
        <Navbar.Brand>
          <Link to="/admin/">
            <img style={{ width: 100 }} src={Logo}></img>
          </Link>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto admin-nav-links">
            <Nav.Link>
              <Link to="/admin">Vendors</Link>
            </Nav.Link>
            <Nav.Link>
              <Link to="/admin/reservations">Reservations</Link>
            </Nav.Link>
            <Nav.Link>
              <Link to="/admin/discount">Discount Vouchers</Link>
            </Nav.Link>
            <Nav.Link>
              <Link to="/admin/complaints">Complaints</Link>
            </Nav.Link>
            <Nav.Link>
              <Link to="/admin/slideshow">Slideshow</Link>
            </Nav.Link>
            <Dropdown>
              <Dropdown.Toggle variant="link" id="dropdown-basic">
                <ion-icon name="notifications-outline"></ion-icon>
                <Badge pill className="notifications" variant="primary">
                  {vendors.length}
                </Badge>
              </Dropdown.Toggle>

              <Dropdown.Menu>
                {notifications.length ? (
                  notifications
                ) : (
                  <Dropdown.Item>No notifications</Dropdown.Item>
                )}
              </Dropdown.Menu>
            </Dropdown>
          </Nav>
          <Link>
            <Button
              onClick={this.logout}
              type="button"
              variant="outline-primary"
            >
              Logout
            </Button>
          </Link>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(AdminNavbar)
);
