import React, { Component } from "react";
import { Container, Card, Table, Badge, Form, Col } from "react-bootstrap";
import { withRouter } from "react-router-dom";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import { connect } from "react-redux";

import Footer from "../../../Components/FooterAndButtons/FooterAndButtons";
import Header from "../../../Components/Sidebar/Header";
class Reservations extends Component {
  state = {
    vendor: "",
    customer: "",
    minDate: null,
    maxDate: null,
    vendorType: "All"
  };
  redirectToCustomer = async uid => {
    this.props.history.push("/admin/customer/" + uid);
  };
  render() {
    const reservationsToRender = this.props.reservations.map(element => {
      const customer = this.props.customers.find(
        c => c.uid === element.customer_uid
      );
      const vendor = this.props.vendors.find(
        c => c.uid === element.restaurant_uid
      );

      const date = new Date(element.date);
      const minDate = new Date(this.state.minDate);
      const maxDate = new Date(this.state.maxDate);
      const condition =
        vendor.name
          .toLowerCase()
          .includes(this.state.vendor.toLowerCase(), 0) &&
        (this.state.vendorType === "All" ||
          this.state.vendorType === vendor.category) &&
        customer.name
          .toLowerCase()
          .includes(this.state.customer.toLowerCase(), 0) &&
        (!this.state.minDate ||
          !this.state.maxDate ||
          (date >= minDate && date <= maxDate));
      const badgeVariant =
        element.status == "Reserved"
          ? "warning"
          : element.status == "Completed"
          ? "success"
          : "danger";
      return condition ? (
        <tr onClick={() => this.redirectToCustomer(customer.uid)}>
          <td>
            <img
              style={{
                width: 40,
                height: 40,
                borderRadius: 50,
                objectFit: "cover"
              }}
              src={customer.photo}
            ></img>
          </td>
          <td>{customer.name}</td>
          <td>{vendor ? vendor.name : "Vendor Deleted"}</td>
          <td>{element.time}</td>
          <td>{element.date}</td>
          <td>{element.attandee}</td>
          <td>
            {Math.floor(Number(element.attandee / 3)) == 0
              ? 1
              : Math.floor(Number(element.attandee / 3))}
          </td>
          <td>
            <Badge variant={badgeVariant}>{element.status}</Badge>
          </td>
        </tr>
      ) : null;
    });
    return (
      <div>
        <Header>
          <Container>
            <Card className="my-3">
              <Card.Header>
                <h1>Reservations</h1>
              </Card.Header>
              <Card.Body>
                <Form.Row style={{ paddingTop: 10 }}>
                  <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Vendor Type</Form.Label>
                    <Form.Control
                      as="select"
                      onChange={e =>
                        this.setState({ vendorType: e.target.value })
                      }
                    >
                      <option value="All">All</option>
                      <option value="Restaurants">Restaurants</option>
                      <option value="Cafes">Cafes</option>
                      <option value="Escape Rooms">Escape Rooms</option>
                      <option value="Events">Events</option>
                    </Form.Control>
                  </Form.Group>
                </Form.Row>
                <Form.Row style={{ paddingTop: 10 }}>
                  <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Vendor Name</Form.Label>
                    <Form.Control
                      onChange={e => this.setState({ vendor: e.target.value })}
                      placeholder="Search By Vendor"
                    />
                  </Form.Group>
                  <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Customer Name</Form.Label>
                    <Form.Control
                      onChange={e =>
                        this.setState({ customer: e.target.value })
                      }
                      placeholder="Search By Customer"
                    />
                  </Form.Group>
                </Form.Row>
                <Form.Row style={{ paddingTop: 10 }}>
                  <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Min Date</Form.Label>
                    <Form.Control
                      type="date"
                      onChange={e => this.setState({ minDate: e.target.value })}
                    />
                  </Form.Group>
                  <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Max Date</Form.Label>
                    <Form.Control
                      type="date"
                      onChange={e => this.setState({ maxDate: e.target.value })}
                    />
                  </Form.Group>
                </Form.Row>
                <Table responsive striped hover>
                  <thead>
                    <tr>
                      <th>Customer Image</th>
                      <th>Customer Name</th>
                      <th>Vendor Name</th>
                      <th>Reservation Time</th>
                      <th>Date of Reservation</th>
                      <th>Number of Attendees</th>
                      <th>Number of Tables</th>
                      <th>Status</th>
                    </tr>
                  </thead>
                  <tbody>{reservationsToRender}</tbody>
                </Table>
              </Card.Body>
            </Card>
          </Container>
          <Footer></Footer>
        </Header>
      </div>
    );
  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Reservations));
