import React, { Component } from "react";
import {
  Container,
  Card,
  Row,
  Col,
  Form,
  Button,
  Carousel,
  Spinner
} from "react-bootstrap";
import { withRouter } from "react-router-dom";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import { connect } from "react-redux";
import Firebase from "../../../Firebase/Firebase";

import Footer from "../../../Components/FooterAndButtons/FooterAndButtons";
import Header from "../../../Components/Sidebar/Header";
class Slideshow extends Component {
  state = {
    file: null,
    vendor_uid: "",
    isAdding: false
  };
  handler = (name, value) => {
    this.setState({
      [name]: value
    });
  };
  add = async () => {
    this.setState({
      isAdding: true
    });
    const fileNameSplit = this.state.file.name.split(".");
    const extension = fileNameSplit[fileNameSplit.length - 1];
    await Firebase.addSlideShow(
      this.state.file,
      extension,
      this.state.vendor_uid
    );
    window.location.reload();
  };
  fileHandler = e => {
    this.setState({
      file: e.target.files[0]
    });
  };
  delete = async uid => {
    await Firebase.deleteSlideshow(uid);
    window.location.reload();
  };
  render() {
    const vendors = this.props.vendors.map(e => {
      return <option value={e.uid}>{e.name}</option>;
    });
    const slideshows = this.props.slideshows.map(e => {
      const vendor = this.props.vendors.find(v => v.uid === e.vendor_uid);
      return (
        <Carousel.Item>
          <img
            style={{
              height: 500,
              objectFit: "cover"
            }}
            className="d-block w-100"
            src={e.image_url}
          />
          <Carousel.Caption
            style={{
              background: "rgba(0,0,0,0.5)",
              padding: 10,
              borderRadius: 10
            }}
          >
            <h3>{vendor.name}</h3>
            <Button
              variant="danger"
              type="button"
              style={{
                margin: 15
              }}
              onClick={() => this.delete(e.uid)}
            >
              Delete
            </Button>
          </Carousel.Caption>
        </Carousel.Item>
      );
    });
    return (
      <div>
        <Header>
          <Container>
            <Row className="justify-content-center">
              <Col xs={12}>
                <Card>
                  <Card.Header>
                    <h1>Slideshow</h1>
                  </Card.Header>
                  <Card.Body>
                    {slideshows.length > 0 ? (
                      <Carousel>{slideshows}</Carousel>
                    ) : (
                      <p>No Slideshows have been added yet</p>
                    )}
                    <hr></hr>
                    <Form>
                      <legend className="h4">Add New Banner</legend>
                      <Form.Group controlId="exampleForm.ControlSelect1">
                        <Form.Label>Vendor</Form.Label>
                        <Form.Control
                          onChange={e =>
                            this.handler("vendor_uid", e.target.value)
                          }
                          as="select"
                        >
                          {vendors}
                        </Form.Control>
                      </Form.Group>
                      <Form.Group controlId="formGridPassword">
                        <Form.Label>Image</Form.Label>
                        <Form.Control
                          onChange={this.fileHandler}
                          type="file"
                          placeholder="Image"
                          accept="image/*"
                        />
                      </Form.Group>
                      <Button
                        onClick={this.add}
                        block
                        variant="primary"
                        type="button"
                      >
                        {this.state.isAdding ? (
                          <Spinner animation="border" variant="primary" />
                        ) : (
                          "Add Banner"
                        )}
                      </Button>
                    </Form>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
          <Footer></Footer>
        </Header>
      </div>
    );
  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Slideshow));
