import React, { Component } from "react";
import "./Header.css";
import { Link, withRouter } from "react-router-dom";
import Logo from "../../Images/logo.png";
import { connect } from "react-redux";
import { mapStateToProps, mapDispatchToProps } from "../../Config/AdminMapping";
import { removeAdmin } from "../../Config/localStorage";
import { Dropdown, Badge } from "react-bootstrap";
class Header extends Component {
  logout = async () => {
    removeAdmin();
    this.props.updateLogin(null);
    this.props.history.push("/admin/");
  };
  render() {
    const vendors = this.props.vendors.filter(e => !e.is_verified);
    const notifications = vendors.map(e => {
      return (
        <Dropdown.Item>
          <Link to={"/admin/vendor/" + e.uid}>Verify New Vendor {e.name}</Link>
        </Dropdown.Item>
      );
    });
    return (
      <div>
        <nav class="navbar navbar-custom navbar-expand-lg navbar-light bg-white ">
          <Link class="navbar-brand" to="/admin/">
            <img style={{ width: 80 }} src={Logo}></img>
          </Link>
          <button
            class="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarCollapse"
            aria-controls="navbarCollapse"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span class="navbar-toggler-icon "></span>
          </button>

          <div class="collapse navbar-collapse" id="navbarCollapse">
            <ul
              class="navbar-nav mr-auto sidenav h-100 bg-white "
              id="navAccordion"
            >
              <li class="nav-item active">
                <Link to="/admin/">
                  <img
                    className="nav-logo"
                    style={{ width: 200, margin: "70px 40px " }}
                    src={Logo}
                  ></img>
                </Link>
                <div class="mx-3">
                  <li class="nav-item">
                    <Link to="/admin/" class="nav-link h5">
                      <span className="ml-2">Vendors</span>
                    </Link>
                  </li>
                  <li class="nav-item">
                    <Link to="/admin/reservations" class="nav-link h5">
                      <span className="ml-2">Reservations</span>
                    </Link>
                  </li>
                  <li class="nav-item">
                    <Link to="/admin/verification" class="nav-link h5">
                      <span className="ml-2">Vendors Verification</span>
                    </Link>
                  </li>
                  <li class="nav-item">
                    <Link to="/admin/complaints" class="nav-link h5">
                      <span className="ml-2">Complaints</span>
                    </Link>
                  </li>
                  <li class="nav-item">
                    <Link to="/admin/discount" class="nav-link h5">
                      <span className="ml-2">Discount Coupons</span>
                    </Link>
                  </li>
                  <li class="nav-item">
                    <Link to="/admin/slideshow" class="nav-link h5">
                      <span className="ml-2">Slideshow Banners</span>
                    </Link>
                  </li>

                  <li class="nav-item">
                    <Link onClick={this.logout} class="nav-link h5 ">
                      <span className="ml-2">Logout</span>
                    </Link>
                  </li>
                </div>
              </li>
            </ul>
            <form class="form-inline ml-auto mt-2 mt-md-0"></form>
            <div class="d-flex align-items-center mx-3">
              <Dropdown>
                <Dropdown.Toggle variant="link" id="dropdown-basic">
                  <ion-icon name="notifications-outline"></ion-icon>
                  <Badge pill className="notifications" variant="primary">
                    {vendors.length}
                  </Badge>
                </Dropdown.Toggle>

                <Dropdown.Menu>
                  {notifications.length ? (
                    notifications
                  ) : (
                    <Dropdown.Item>No notifications</Dropdown.Item>
                  )}
                </Dropdown.Menu>
              </Dropdown>
            </div>
          </div>
        </nav>
        <main class="content-wrapper">{this.props.children}</main>
      </div>
    );
  }
}
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Header));
