import { AdminActionTypes } from "../Actions/ActionTypes";

const initialState = {
  loggedInAdmin: null,
  reservations: [],
  vendors: [],
  notifications: [],
  complaints: [],
  vouchers: [],
  reviews: [],
  customers: [],
  slideshows: []
};
const AdminReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case AdminActionTypes.ADMIN_UPDATE_LOGIN_STATUS:
      return { ...state, loggedInAdmin: payload };

    case AdminActionTypes.UPDATE_COMPLAINTS:
      return { ...state, complaints: payload };

    case AdminActionTypes.UPDATE_DISCOUNT_VOUCHERS:
      return { ...state, vouchers: payload };

    case AdminActionTypes.UPDATE_NOTIFICATIONS:
      return { ...state, notifications: payload };

    case AdminActionTypes.UPDATE_RESERVATIONS:
      return { ...state, reservations: payload };

    case AdminActionTypes.UPDATE_REVIEWS:
      return { ...state, reviews: payload };

    case AdminActionTypes.UPDATE_VENDORS:
      return { ...state, vendors: payload };

    case AdminActionTypes.UPDATE_CUSTOMERS:
      return { ...state, customers: payload };
    case AdminActionTypes.UPDATE_SLIDESHOW:
      return { ...state, slideshows: payload };
    default:
      return state;
  }
};

export default AdminReducer;
