import React, { Component } from "react";
import Header from "../../../Components/Sidebar/Header";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import {
  Table,
  Button,
  Badge,
  Card,
  Col,
  Row,
  Container,
  Form,
  Modal,
  InputGroup,
  FormControl
} from "react-bootstrap";
import FooterAndButtons from "../../../Components/FooterAndButtons/FooterAndButtons";
import Firebase from "../../../Firebase/Firebase";

class VendorVerifications extends Component {
  state = {
    modalShow: false,
    selectedVendor: "",
    procFee: 0,
    date: new Date(),
    name: ""
  };
  clickHandler = async uid => {
    this.props.history.push("/admin/vendor/" + uid);
  };
  render() {
    const vendors = this.props.vendors.filter(element => {
      const condition = element.name
        .toLowerCase()
        .includes(this.state.name.toLowerCase(), 0);
      return condition;
    });
    const unverified = vendors.filter(element => !element.is_verified);
    return (
      <div>
        <Header>
          <Container>
            <Row className="justify-content-center">
              <Col xs={12}>
                <Card>
                  <Card.Header>
                    <h1>Vendors Verification</h1>
                  </Card.Header>
                  <Card.Body>
                    <Form.Row style={{ paddingTop: 10 }}>
                      <Form.Group as={Col} controlId="formGridCity">
                        <Form.Control
                          onChange={e =>
                            this.setState({ name: e.target.value })
                          }
                          placeholder="Search"
                        />
                      </Form.Group>
                    </Form.Row>
                    <Table striped hover responsive>
                      <thead>
                        <tr>
                          <th>Image</th>
                          <th>Name</th>
                          <th>Email</th>
                          <th>Reservation Price</th>
                          <th>Status</th>
                          <th></th>
                        </tr>
                      </thead>
                      <tbody>
                        {unverified.map(element => {
                          return (
                            <>
                              <tr>
                                <td
                                  onClick={() => this.clickHandler(element.uid)}
                                >
                                  <img
                                    style={{
                                      width: 40,
                                      height: 40,
                                      borderRadius: 50,
                                      objectFit: "cover"
                                    }}
                                    src={
                                      element.photo_url
                                        ? element.photo_url
                                        : "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRwYsBVC-oKRvO87aXVfE63jYe52NcDYM5ArsCRKAGZaImUHAql"
                                    }
                                  ></img>
                                </td>
                                <td>{element.name}</td>
                                <td>{element.email}</td>
                                <td>{element.reservation_fee}</td>
                                <td>
                                  {element.is_verified ? (
                                    <Badge variant="success">Verified</Badge>
                                  ) : (
                                    <Badge variant="danger">Unerified</Badge>
                                  )}
                                </td>
                                <td>
                                  <Button
                                    onClick={() => {
                                      this.setState({
                                        modalShow: true,
                                        selectedVendor: element.uid
                                      });
                                    }}
                                    variant="success"
                                    size="sm"
                                  >
                                    Verify
                                  </Button>
                                </td>
                              </tr>
                            </>
                          );
                        })}
                      </tbody>
                    </Table>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>{" "}
          <FooterAndButtons></FooterAndButtons>
        </Header>
        <Modal
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
          show={this.state.modalShow}
          onHide={() => this.setState({ modalShow: false })}
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Edit Processing Fee
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <InputGroup className="mb-3">
              <InputGroup.Prepend>
                <InputGroup.Text id="inputGroup-sizing-default">
                  Processing Fee
                </InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                aria-label="Default"
                aria-describedby="inputGroup-sizing-default"
                type="number"
                onChange={e => {
                  this.setState({
                    newProFee: e.target.value
                  });
                }}
              />
            </InputGroup>
          </Modal.Body>
          <Modal.Footer>
            <Button
              onClick={async () => {
                await Firebase.updateProcessingFeeAndVerify(
                  this.state.newProFee,
                  this.state.selectedVendor
                );
                this.setState({ modalShow: false });
              }}
            >
              Update
            </Button>
            <Button
              variant="danger"
              onClick={() => this.setState({ modalShow: false })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(VendorVerifications)
);
