import React, { Component } from "react";
import Logo from "../../Images/logo.png";
import "./HomeNavbar.css";
import { Link } from "react-router-dom";
export default class HomeNavbar extends Component {
  render() {
    return (
      <nav className="home-navbar navbar navbar-expand-lg navbar-light bg-light">
        <Link className="navbar-brand" to="/">
          <img
            style={{
              width: "100px"
            }}
            src={Logo}
          ></img>
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto"></ul>
          <div className="login-buttons my-2 my-lg-0">
            <Link
              to="/vendor/"
              className="btn text-light btn-primary my-2 my-sm-0"
            >
              Vendor Login
            </Link>
            <Link
              to="/admin/"
              className="btn text-light btn-primary mx-2 my-2 my-sm-0"
            >
              Admin Login
            </Link>
          </div>
        </div>
      </nav>
    );
  }
}
