const restrictOrder = async (value, uid) => {
  await app
    .database()
    .ref("/restaurants/" + uid)
    .update({
      is_restrict_order: value
    });
};
const updateProcessingFee = async (value, uid) => {
  await app
    .database()
    .ref("/restaurants/" + uid)
    .update({
      processing_fee: value + ""
    });
};
const updateProcessingFeeAndVerify = async (value, uid) => {
  await app
    .database()
    .ref("/restaurants/" + uid)
    .update({
      processing_fee: value + "",
      is_number_verified: true,
      is_verified: true
    });
};
const addSlideShow = async (file, extension, vendor_uid) => {
  const key = (
    await app
      .database()
      .ref("/slideshows/")
      .push()
  ).key;
  console.log("uid = ", key);
  const image_url = await uploadSlideShowImage(file, key, extension);
  console.log("image_url = ", image_url);
  await app
    .database()
    .ref("/slideshows/" + key)
    .set({
      uid: key,
      vendor_uid,
      image_url
    });
};
const uploadSlideShowImage = async (file, uid, extension) => {
  console.log("uu uid = ", uid);
  console.log("extension = ", extension);
  console.log("file = ", file);
  const ref = app.storage().ref("/slideshows/");
  var imageRef = ref.child(uid + "." + extension);
  const response = await imageRef.put(file);
  return response.ref.getDownloadURL();
};
const Firebase = {
  login,
  getAdmin,
  getReservations,
  getRestaurants,
  getDishes,
  getReviews,
  getAllReviews,
  getComplaints,
  getCustomers,
  getNotifications,
  addDiscountVoucher,
  getDiscountVouchers,
  listenToNotifications,
  listenToVendors,
  listenToReviews,
  listenToComplaints,
  listenToDiscountVouchers,
  listenToReservations,
  listenToDishes,
  listenToCustomers,
  deleteVendor,
  restrictOrder,
  updateProcessingFee,
  updateProcessingFeeAndVerify,
  updateDiscountVoucher,
  addSlideShow,
  listenToSlideShows,
  getSlideShows,
  deleteSlideshow,
  deleteDiscount
};
export default Firebase;
